<template>
  <main class="content container">
    <div style="height: 500px; width: 500px;" v-if="orderLoading">
      <img src="../../public/img/1484.gif" alt="1484.gif" >
    </div>
    <div v-else="!orderLoading"> 
      <div class="content__top">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
          <router-link :to="{name: 'main'}" class="breadcrumbs__link">
              Каталог
          </router-link>
          </li>
          <li class="breadcrumbs__item">
            <router-link :to="{name: 'cart'}" class="breadcrumbs__link">
              Корзина
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link">
              Оформление заказа
            </a>
          </li>
        </ul>

        <h1 class="content__title">
          Заказ оформлен <span>№ {{ outputOrder.id }}</span>
        </h1>
      </div>
      <section class="cart">
        <form class="cart__form form" action="#" method="POST">
          <div class="cart__field">
            <p class="cart__message">
              Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа. 
              Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
            </p>

            <ul class="dictionary">
              <li class="dictionary__item">
                <span class="dictionary__key">
                  Получатель
                </span>
                <span class="dictionary__value">
                  {{ outputOrder.name }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key">
                  Адрес доставки
                </span>
                <span class="dictionary__value">
                  {{ outputOrder.address }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key">
                  Телефон
                </span>
                <span class="dictionary__value">
                  {{ outputOrder.phone }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key">
                  Email
                </span>
                <span class="dictionary__value">
                  {{ outputOrder.email }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key">
                  Способ оплаты
                </span>
                <span class="dictionary__value">
                  картой при получении
                </span>
              </li>
            </ul>
          </div>
          <div class="cart__block">
            <ul class="cart__orders">
              <li class="cart__order" v-for="item in outputBasket">
                <h3>{{ item.product.title }}</h3>
                <b>{{ (item.product.price * item.quantity) | numberFormat}} ₽</b>
                <span>Артикул: {{ item.product.id }}</span>
              </li>
            </ul>
            
            <div class="cart__total">
              <p>Доставка: <b>500 ₽</b></p>
              <p>Итого: <b>{{ amountProducts }}</b> товара на сумму <b>{{ orderData.totalPrice | numberFormat }} ₽</b></p>
            </div>
          </div>
        </form>
      </section>
    </div>
  </main>
</template>

<script>
  import numberFormat from '@/helpers/numberFormat';
  import axios from 'axios';
  import { API_BASE_URL } from '@/config';
  import router from '@/router';

  export default {
    data() {
      return {
          orderData: {},
          orderLoading: false,
      };
    },
    filters: { numberFormat },
    computed: {
      outputOrder() {
        return this.orderData;
      },
      outputBasket() {
        return this.orderData.basket ? this.orderData.basket.items : {};
      },
      amountProducts() {
        let amount = 0;
        if (this.orderData.basket ) {
          for (let item of this.orderData.basket.items) {
            amount += item.quantity;
          }
          return amount;
        }
      }
    },
    methods: {
      loadOrder() {
        this.orderLoading = true;
        clearTimeout(this.loadOrderTimer);
        this.loadOrderTimer = setTimeout(() => {
          axios
          .get(API_BASE_URL + "/api/orders/" + this.$route.params.id, {
            params: {
              userAccessKey: this.$store.state.userAccessKey
            }
          })
          .then((response) => this.orderData = response.data)
          .then(() => this.orderLoading = false);
        }, 3000);
      },
    },
    created() {
      this.loadOrder();
      // if(this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) {
      //   return;
      // }
      // this.$store.dispatch('loadOrderInfo', this.$route.params.id);
    },
    components: { router }
}
</script>